@import url(https://fonts.googleapis.com/css2?family=Pattaya&display=swap);
.App {
  text-align: center;
  font-family: "Comic Sans MS", sans-serif;
}

.body {
  background-color: #fbfbfb;
}

#root {
  background-color: #292929;
}

/*********************

HOME

*******************/
.background {
  box-shadow: inset 0 0 0 2000px rgba(1, 79, 124, 0.3);
  background: url(/static/media/james-barr-lf8nwCdrx00-unsplash.65c88f93.jpg) no-repeat center
    center/cover;
  background-position: center;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: flex;
  padding: 0px 140px;
  flex-direction: column;
  justify-content: center;
}

.background .button {
  margin-top: 20px;
  width: 22%;
  cursor: pointer;
  z-index: 99;
}
.slogan h1 {
  color: white;
  text-align: left;
  font-size: 40px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
span {
  color: #00adff;
  font-size: 60px;
  font-family: "Pattaya", sans-serif !important;
  text-shadow: 2px 2px #000000;
}

.title {
  text-shadow: 2px 2px #000000;
  color: rgb(226, 226, 226);
  text-align: left;
}

@media screen and (max-width: 850px) {
  .background .button {
    width: 200px;
  }
  .background {
    padding: 50px;
  }
  .slogan h1 {
    font-size: 4vw;
  }
  span {
    font-size: 8vw;
  }
}

/*********************

SCHEDULE

*******************/
.schedule {
  background-color: #fbfbfb;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 90vh;
  align-items: center;
}

.schedule-header {
  color: black;
  text-transform: uppercase;
}

.times div h2 {
  font-weight: normal;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  padding: 25px;
}

.times div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.schedule .button {
  width: 20%;
  margin-top: 20px;
}

@media screen and (max-width: 600px) {
  .schedule .button {
    width: auto;
  }
}

/*********************

CONTACT

*******************/
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Quicksand", sans-serif;
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 100px;
  height: 90vh;
  background-color: #555555;
}

.container:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: url(/static/media/james-barr-lf8nwCdrx00-unsplash.65c88f93.jpg) no-repeat center;
  background-size: cover;
  filter: blur(50px);
  z-index: -1;
}
.contact-box {
  max-width: 850px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #fff;
  box-shadow: 0px 0px 19px 5px rgba(0, 0, 0, 0.19);
}

.left {
  background: url(/static/media/james-barr-lf8nwCdrx00-unsplash.65c88f93.jpg) no-repeat center;
  background-size: cover;
  height: 100%;
}

.right {
  padding: 25px 40px;
}

h2 {
  position: relative;
  padding: 0 0 10px;
  margin-bottom: 10px;
}

h2:after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  height: 4px;
  width: 100px;
  border-radius: 2px;
  background-color: #00adff;
}

.field {
  width: 100%;
  border: 2px solid rgba(0, 0, 0, 0);
  outline: none;
  background-color: rgba(230, 230, 230, 0.6);
  padding: 0.5rem 1rem;
  font-size: 1.1rem;
  margin-bottom: 22px;
  transition: 0.3s;
  resize: none;
}

.field:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

textarea {
  min-height: 150px;
}

.btn {
  width: 100%;
  padding: 0.5rem 1rem;
  background-color: #00adff;
  color: #fff;
  font-size: 1.1rem;
  border: none;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
}

.btn:hover {
  background-color: rgb(0, 101, 255);
}

.field:focus {
  border: 2px solid rgba(30, 85, 250, 0.47);
  background-color: #fff;
}

@media screen and (max-width: 880px) {
  .contact-box {
    grid-template-columns: 1fr;
  }
  .left {
    height: 200px;
  }
}

/*********************

About

*******************/

.section {
  width: 100%;
  min-height: 100vh;
  background-color: #ddd;
}
.about-container {
  width: 80%;
  display: block;
  margin: auto;
  padding-top: 100px;
}
.content-section {
  float: left;
  width: 55%;
}
.image-section {
  float: right;
  width: 40%;
}
.image-section img {
  width: 100%;
  height: auto;
}
.content-section .title {
  text-transform: uppercase;
  font-size: 28px;
  color: #00adff;
  text-align: center;
}
.content-section .content h3 {
  margin-top: 20px;
  color: #5d5d5d;
  font-size: 21px;
}
.content-section .content p {
  margin-top: 10px;
  font-family: sans-serif;
  font-size: 18px;
  line-height: 1.5;
}
.content-section .content .about-button {
  margin-top: 30px;
}
.content-section .content .about-button a {
  background-color: #3d3d3d;
  padding: 12px 40px;
  text-decoration: none;
  color: #fff;
  font-size: 25px;
  letter-spacing: 1.5px;
}
.content-section .content .about-button a:hover {
  background-color: #00adff;
  color: #fff;
}
.content-section .social {
  margin: 40px 40px;
}
.content-section .social i {
  color: #a52a2a;
  font-size: 30px;
  padding: 0px 10px;
}
.content-section .social i:hover {
  color: #3d3d3d;
}
@media screen and (max-width: 768px) {
  .about-container {
    width: 80%;
    display: block;
    margin: auto;
    padding-top: 50px;
  }
  .content-section {
    float: none;
    width: 100%;
    display: block;
    margin: auto;
  }
  .image-section {
    float: none;
    width: 100%;
  }
  .image-section img {
    width: 100%;
    height: auto;
    display: block;
    margin: auto;
    margin-top: 20px;
  }
  .content-section .title {
    text-align: center;
    font-size: 19px;
  }
  .content-section .content .button {
    text-align: center;
  }
  .content-section .content .button a {
    padding: 9px 30px;
  }
  .content-section .social {
    text-align: center;
  }
}

* {
  margin: 0;
  padding: 0;
  font-family: "Comic Sans MS", sans-serif;
  box-sizing: border-box;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  height: 65px;
  position: fixed;
  width: 100%;
  padding: 0px 50px;
  border-bottom: 1px solid #ccc;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.3);
  z-index: 100;
}
.nav_logo {
  width: 90px;
}
.section1 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.section2 {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.nav_list {
  display: flex;
  justify-content: flex-end;
}
.logo {
  width: 75px;
}

.nav_item {
  display: inline-block;
  vertical-align: middle;
  padding: 15px;
  text-decoration: none;
}

.nav-link {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-decoration: none;
  font-size: 13px;
  color: black;
  font-weight: 500;
  letter-spacing: 0px;
  text-transform: uppercase;
  padding: 5px;
}
.nav_item a:active {
  color: black;
}

.nav_item a:visited {
  color: black;
}

.nav_item:hover {
  cursor: pointer;
}
.nav_item:hover .nav-link {
  border-bottom: 1px solid black;
  cursor: pointer;
}

.burger {
  display: none;
}

.button {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-decoration: none;
  font-size: 13px;
  color: white;
  font-weight: 550;
  letter-spacing: 0px;
  text-transform: uppercase;
  padding: 10px 20px;
  background-color: #00adff;
  cursor: pointer;
  z-index: 10;
}

.button:hover {
  background-color: rgb(0, 101, 255);
  transition: 0.2s;
}
.social-height {
  height: 20px;
  padding: 0px 10px;
}

.socials {
  display: flex;
  padding: 0px 30px;
  align-items: center;
  justify-content: space-between;
}

.social_icon {
  width: 20px;
}

.spacer {
  height: 65px;
}
.drop {
  display: none;
}
#drop {
  display: none;
}
.drop-down {
  display: flex;
  flex-direction: column;
  position: absolute;
  margin-top: 5px;
  text-align: left;
}

.drop-down a:hover {
  background-color: #00adff;
}

.drop-down a {
  background-color: white;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-decoration: none;
  font-size: 13px;
  color: black;
  font-weight: 500;
  letter-spacing: 0px;
  text-transform: uppercase;
  padding: 10px 5px;
}

.ministry:hover .drop {
  display: block;
}
.nav_resp {
  display: none;
}

.show-nav {
  display: block !important;
}
.nav_list2 {
  height: 100vh;
  background-color: white;
  display: flex;
  position: absolute;
  flex-direction: column;
  justify-content: center;
  z-index: 101;
  width: 100%;
}
.nav_list2 .nav_item {
  padding: 20px;
}
.nav_list2 .nav_item:hover {
  background: #00adff;
}
.nav_list2 .nav_item .nav-link {
  font-size: 30px;
  border: none !important;
}

.nav_close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 10px;
  width: 50px;
}
@media screen and (max-width: 1030px) {
  nav {
    padding: 0px 10px;
  }

  .section1 a img {
    width: 60px;
  }
  .nav_item {
    padding: 7px;
  }
}
@media screen and (max-width: 850px) {
  .nav_list {
    display: none;
  }

  .section1 a img {
    width: 100px;
    padding: 7px;
  }
  .burger {
    display: block;
    width: 30px;
    margin-right: 20px;
  }
  .drop-down {
    position: relative;
  }
}

@media screen and (max-width: 580px) {
  .socials {
    display: none;
  }
  .button {
    width: 140px;
  }
  h2 {
    font-size: 20px;
  }

  .user {
    width: auto;
    padding: 0px !important;
  }

  .user p {
    font-size: 15px !important;
  }

  .nav-end {
    justify-content: flex-end;
    margin-right: 5px;
  }

  .container {
    padding: 10px 20px;
  }
  .icon {
    width: 30px;
    border: none !important;
    max-height: 4vh !important;
    padding: 0px !important;
  }
}

@media screen and (max-width: 400px) {
  .button {
    font-size: 10px;
    width: auto;
  }
}

@media screen and (max-width: 768px) {
  .burger {
    display: block;
    width: 10%;
  }
}

